

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


@font-face {
  font-family: 'Satoshi-Variable';
  src: url('../src/fonts/satoshi/Satoshi-Variable.woff2') format('woff2'),
       url('../src/fonts/satoshi/Satoshi-Variable.woff') format('woff'),
       url('../src/fonts/satoshi/Satoshi-Variable.ttf') format('truetype');
       font-weight: 300 900;
       font-display: swap;
       font-style: normal;
}


/**
* This is a variable font
* You can controll variable axes as shown below:
* font-variation-settings: 'wght' 900.0;
*
* available axes:

* 'wght' (range from 300.0 to 900.0)

*/

@font-face {
  font-family: 'Satoshi-VariableItalic';
  src: url('../src/fonts/satoshi/Satoshi-VariableItalic.woff2') format('woff2'),
       url('../src/fonts/satoshi/Satoshi-VariableItalic.woff') format('woff'),
       url('../src/fonts/satoshi/Satoshi-VariableItalic.ttf') format('truetype');
       font-weight: 300 900;
       font-display: swap;
       font-style: italic;
}


